.navbar-class {
  background-color: #f0f0f0;
  color: #333;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  display: flex; /* Use flexbox for layout */
  gap: 15px; /* Add spacing between elements */
}
.navbar-class > * { /* > * indicates each child of the class */
  padding: 5px 10px; /* Add padding to each child element */
  text-decoration: none; /* Optional: Remove underline from links */
  color: inherit; /* Ensure links inherit the navbar text color */
}

.navbar-class-logout-button{
  margin-left: auto; /* Push the logout button to the far right */
}

.search-form-container {
  display: flex; /* Use flexbox for alignment */
  flex-direction: column; /* Stack elements vertically */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  margin: 20px 0; /* Add some vertical spacing */
}

.search-form {
  display: flex; /* Keep input and button in a row */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Align vertically */
  gap: 10px; /* Add spacing between input and button */
  width: 90%;
}

.search-form input {
  width: 50%; /* Make the input larger */
  padding: 10px; /* Add padding for better appearance */
  font-size: 16px; /* Increase font size */
  border: 1px solid #ccc; /* Add a border */
  border-radius: 5px; /* Round the corners */
}

.button {
  padding: 10px 20px; /* Add padding for size */
  border: none; /* Remove border */
  border-radius: 5px; /* Optional: Round the corners */
  font-size: 16px; /* Set font size */
  cursor: pointer; /* Add pointer cursor */
}


.login-container {
  display: flex; /* Use flexbox for alignment */
  flex-direction: column; /* Stack elements vertically */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  height: 70vh;
  margin: 0px 0; 
}
.login-container input {
  margin: 5px 0;
  width: 30%;
  padding: 5px;
}